import { IPaginationProps, IViewProps, Pagination, View } from '@miq/componentjs';
import { getCN } from '@miq/utiljs';
import * as React from 'react';
import { ProductGrid } from './components';

export interface IProductDetailViewProps extends IViewProps {
  images?: React.ReactNode;
  readonly clsPrefix?: 'miq-shop-product-detail-view';
}

export const ProductDetailView: React.FC<IProductDetailViewProps> = (props) => {
  const { clsPrefix, images, children, ...rest } = props;
  return (
    <View {...rest} className={getCN([clsPrefix, props.className])}>
      <div className={`${clsPrefix}-grid d-grid grid-md-12`}>
        {/* media */}
        <div className={`${clsPrefix}-grid-media span-md-8`}>{images}</div>

        {/* product */}
        <div className={`${clsPrefix}-grid-product span-md-4`}>{children}</div>
      </div>
    </View>
  );
};

export interface IProductGridViewProps extends Omit<IViewProps, 'footer'> {
  items: any[];
  viewCN?: string;
  renderItem: (i: any) => JSX.Element | null;
  pagination?: IPaginationProps;
}

export const ProductGridView: React.FC<IProductGridViewProps> = (props) => {
  const { viewCN, pagination, className, children, items, renderItem, ...rest } = props;

  return (
    <View {...rest} className={viewCN} footer={pagination && <Pagination {...pagination} />}>
      <ProductGrid className={className}>{children || items.map((i) => renderItem(i))}</ProductGrid>
    </View>
  );
};

export interface IProductListViewProps {}

export const ProductListView: React.FC<IProductListViewProps> = (props) => {
  return <View {...props} />;
};
