import * as React from 'react';

import './components.scss';

import { SharedDataCtx } from '@miq/contextjs';
import { getCN } from '@miq/utiljs';
import { Img, HorizontalGalleryProps } from '@miq/componentjs';
import { PriceDataType } from './types';

export type ProductPriceProps = {
  retail_price: PriceDataType;
  sale_price?: PriceDataType;
  is_on_sale?: boolean;
};

export const ProductPrice: React.FC<ProductPriceProps> = (props) => {
  const { is_on_sale, retail_price, sale_price } = props;
  const isSale = is_on_sale && sale_price?.amountWithSymbol != null;

  return (
    <div className={getCN(['p-price', isSale && 'sale'])}>
      {isSale && <div className="p-price-sale text-danger me-1">{sale_price.amountWithSymbol}</div>}
      <div className={getCN(['p-price-retail', isSale && 'text-line-through'])}>{retail_price.amountWithSymbol}</div>
    </div>
  );
};

export interface ProductGridProps extends React.ComponentPropsWithoutRef<'div'> {}

export const ProductGrid: React.FC<ProductGridProps> = ({
  className = 'grid-2 grid-md-3 grid-lg-4 gap-2',
  children,
}) => {
  return <div className={getCN(['miq-product-grid d-grid', className])}>{children}</div>;
};

export type CategoryLinksProps = {
  showCover?: boolean | string;
};

export const CategoryLinks = (props: CategoryLinksProps) => {
  const { categories } = React.useContext(SharedDataCtx);

  if (!categories) return null;

  return (
    <div className="p-category-links pb-2">
      {categories?.map((cat) => {
        return (
          <div className="item w-25 w-md-15" key={cat.meta_slug}>
            <a href={cat.url} className="" title={cat.name}>
              {props.showCover && cat?.cover && (
                <Img.Picture {...cat.cover} src_mobile={cat?.cover?.thumb_sq || cat?.cover?.src_mobile} />
              )}
              <div className="info">{cat.name_truncated}</div>
            </a>
          </div>
        );
      })}
    </div>
  );
};

export type ProductImagesHorizontalGalleryProps = HorizontalGalleryProps & {};

export const ProductImagesHorizontalGallery = (props: ProductImagesHorizontalGalleryProps) => {
  return <Img.HorizontalGallery {...props} className={getCN(['p-imgs-hgallery', props.className])} />;
};
